.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  top: 0;
  background-color: #83dff2;
  color: #fff;
 }
 .nav-links a {
  color: #fff;
 }

 .logo {
  font-size: 28px;
 }
 
 .menu {
  display: flex;
  grid-gap: 1em;
  gap: 1em;
  font-size: 18px;
 }
 .menu li:hover {
  background-color: #04cdf5;
  border-radius: 12px;
  transition: 0.3s ease;
 }
 .menu li {
  padding: 5px 14px;
 }

 .services {
  position: relative; 
 }
 .dropdown {
  background-color:#83dff2;
  padding: 1em 0;
  position: absolute; 
  display: none;
  border-radius: 8px;
  top: 35px;
 }
 .dropdown li + li {
  margin-top: 10px;
 }
 .dropdown li {
  padding: 0.5em 1em;
  width: 8em;
  text-align: center;
 }
 .dropdown li:hover {
  background-color: #83dff2;
 }
 .services:hover .dropdown {
  display: block;
 }

 input[type=checkbox]{
  display: none;
 } 
 
 .hamburger {
  display: none;
  font-size: 24px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
 }

 @media (max-width: 768px) {
 .menu { 
  display:none;
  position: absolute;
  background-color:#83dff2;
  right: 0;
  left: 0;
  text-align: center;
  padding: 16px 0;
 }
 .menu li:hover {
  display: inline-block;
  background-color:#04cdf5;
  transition: 0.3s ease;
 }
 .menu li + li {
  margin-top: 12px;
 }
 input[type=checkbox]:checked ~ .menu{
  display: block;
 }
 .hamburger {
  display: block;
 }
 .dropdown {
  left: 50%;
  top: 30px;
  transform: translateX(35%);
 }
 .dropdown li:hover {
  background-color: #04cdf5;
 }
 }
