.btns-theory {
	
    color: #ff70a6;
    width: 150px;
    height: 50px;
        border-radius: 12px;
        border: 2px solid #ff70a6;
    background: transparent;
    cursor: pointer;
    font-size: 16px;
    margin:25px;
        
    }
.btns-theory {
    border-color:  #ff70a6;
    border-radius: 12px;
    border: 2px solid #ff70a6;
    color:  #ff70a6;
    position: relative;
    overflow: hidden;
    z-index: 1;

    background: transparent;
    font-family: 'Yusei Magic', sans-serif;
    transition: color 150ms ease-in-out;
  }
  .btns-theory:after {
    content: '';
    position: absolute;
    display: block;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 100%;
    background: #ff70a6;
    z-index: -1;
    transition: width 150ms ease-in-out;
  }
  .btns-theory:hover {
      text-decoration: none;
    color: #fff;
  }
  .btns-theory:hover:after {
    width: 110%;
  }

  .btns-theory a{
      text-decoration: none;
  }

.btns-creative {
color: #70d2ff;
	width: 150px;
	height: 50px;
border-radius: 12px;
font-family: 'Yusei Magic', sans-serif;
 border: 2px solid #70d2ff;
background: transparent;
cursor: pointer;
font-size: 16px;
}



.btns-creative:hover{
   background-image:     linear-gradient(
      to right, 
      #E7484F,
      #E7484F 16.65%,
      #F68B1D 16.65%,
      #F68B1D 33.3%,
      #FCED00 33.3%,
      #FCED00 49.95%,
      #009E4F 49.95%,
      #009E4F 66.6%,
      #00AAC3 66.6%,
      #00AAC3 83.25%,
      #732982 83.25%,
      #732982 100%,
      #E7484F 100%
     
     
     
    );
  animation:slidebg 2s linear infinite;
}


@keyframes slidebg {
  to {
    background-position:20vw;
  }
}




@keyframes drop {


0% {
opacity: 0;
transform: translateY(-80px);
	}
100% {
opacity: 1;
transform: translateY(0px);
	}
}

